import { makeAutoObservable } from 'mobx';
import { makePersistable } from 'mobx-persist-store';

class radarsState {
	selectedRadarId = null;

	radars = [
		{ id: 1, isWorking: true, name: 'Radar 01', loc: [64.5284, 40.54095], isShow: (JSON.parse(localStorage.getItem('Radar selection')))?.[1] },
	];
	aises = [
		{ id: 1, isWorking: true, name: 'Ais 01', loc: [64.5284, 40.54095], isShow: (JSON.parse(localStorage.getItem('AIS management')))?.[1] },
	];

	constructor() {
		makeAutoObservable(this);

		makePersistable(this, {
			name: 'RadarsStore',
			properties: ['radars', 'aises'],
			storage: window.localStorage,
		});
	}

	setRadars = (radars = []) => {
		this.radars = radars;
	};

	setAises = (arr) => {
		this.aises = arr;
	};

	setSelectedRadarId = (radarId = null) => {
		this.selectedRadarId = radarId;
	};

	setAllRadars = (view) => {
		this.radars.forEach((radar) => {
			radar.isShow = view
		});
	};
	setAllAises = (view) => {
		this.aises.forEach((ais) => {
			ais.isShow = view
		});
	};
}

export default new radarsState();
